<template>
  <div>
    <h1>原神伤害计算器</h1>
    <p>伤害公式：[(人物攻击+武器攻击) * (1+武器、圣遗物、人物的百分比提升)+圣遗物副词缀]*[1+元素加成/物理加成]*[1+爆伤]</p>
    <table border="2px" style="background-color:rgba(54, 181, 219, 0.295);margin-left:10%;width:80%;text-align:center">
      <tr style="height:10px">
        <td colspan="2" >ps：伤害为理想数值，即不计算怪物抗性。武器90、人物90、技能666、圣遗物20、
          武器(0精)满特效、伤害必暴击</td>
      </tr> 
      <!-- 角色行 -->
      <tr>
        <!-- 显示角色信息 -->
        <td style="width:50%">
          <table border="2" style="width:80%;margin:50px">
            <caption>人物区</caption>
            <tr>
              <td style="width:40%">角色</td>
              <td>{{this.role.myRole.name}}</td>
            </tr>
            <tr>
              <td>生命值</td>
              <td>{{this.role.myRole.shengmingzhi}}</td>
            </tr>
            <tr>
              <td>攻击力</td>
              <td>{{this.role.myRole.gongjili}}</td>
            </tr>
            <tr>
              <td>防御力</td>
              <td>{{this.role.myRole.fangyuli}}</td>
            </tr>
            <tr>
              <td>城市</td>
              <td>{{this.role.myRole.city}}</td>
            </tr>
            <tr>
              <td>生日</td>
              <td>{{this.role.myRole.birth}}</td>
            </tr>
            <tr v-if="this.role.myRole.yuansujiacheng!=0">
              <td>元素加成</td>
              <td>{{this.role.myRole.yuansujiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.wulijiacheng!=0">
              <td>物理加成</td>
              <td>{{this.role.myRole.wulijiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.gongjilijiacheng!=0">
              <td>攻击加成</td>
              <td>{{this.role.myRole.gongjilijiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.shengmingzhijiacheng!=0">
              <td>生命加成</td>
              <td>{{this.role.myRole.shengmingzhijiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.fangyulijiacheng!=0">
              <td>防御加成</td>
              <td>{{this.role.myRole.fangyulijiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.baojilvjiacheng!=0">
              <td>暴击率加成</td>
              <td>{{this.role.myRole.baojilvjiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.baoshangjiacheng!=0">
              <td>爆伤加成</td>
              <td>{{this.role.myRole.baoshangjiacheng}}</td>
            </tr>
            <tr v-if="this.role.myRole.yuansujingtongjiacheng!=0">
              <td>元素精通加成</td>
              <td>{{this.role.myRole.yuansujingtongjiacheng}}</td>
            </tr>
          </table>
        </td>
        <!-- 选择角色 -->
        <td style="width:50%">
          属性：
          <el-select v-model="role.type" @change="roleTypeChange" placeholder="请选择人物属性">
            <el-option v-for="(item,i) in role.typeList" :key="i" :label="item" :value="item"></el-option>
          </el-select><br>
          <p v-if="role.list.length==0">对不起，该属性暂无角色！</p>
          <el-radio v-for="(item ,i) in role.list" 
            :key="i" :label="item" 
            v-model="role.choose"
            style="margin-top:20px">
            {{item}}
          </el-radio>
        </td>
      </tr>
      <!-- 武器行 -->
      <tr>
        <!-- 显示武器信息 -->
        <td style="width:50%">
          <table border="2" style="width:80%;margin:50px">
            <caption>武器区</caption>
            <tr>
              <td style="width:40%">武器</td>
              <td style="width:60%">{{this.weapon.myWeapon.name}}</td>
            </tr>
            <tr>
              <td>攻击力</td>
              <td>{{this.weapon.myWeapon.gongjili}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.gongjilijiacheng!=0">
              <td>攻击力加成</td>
              <td>{{this.weapon.myWeapon.gongjilijiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.baojilvjiacheng!=0">
              <td>暴击率加成</td>
              <td>{{this.weapon.myWeapon.baojilvjiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.baoshangjiacheng!=0">
              <td>爆伤加成</td>
              <td>{{this.weapon.myWeapon.baoshangjiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.shengmingzhijiacheng!=0">
              <td>生命值加成</td>
              <td>{{this.weapon.myWeapon.shengmingzhijiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.fangyulijiacheng">
              <td>防御力加成加成</td>
              <td>{{this.weapon.myWeapon.fangyulijiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.yuansuchongnengxiaolv">
              <td>元素充能效率</td>
              <td>{{this.weapon.myWeapon.yuansuchongnengxiaolv}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.yuansujingtongjiacheng">
              <td>元素精通</td>
              <td>{{this.weapon.myWeapon.yuansujingtongjiacheng}}</td>
            </tr>
            <tr v-if="this.weapon.myWeapon.wulijiacheng">
              <td>物理加成</td>
              <td>{{this.weapon.myWeapon.wulijiacheng}}</td>
            </tr>
            <tr>
              <td>特效说明</td>
              <td>{{this.weapon.myWeapon.texiao}}</td>
            </tr>
          </table>
        </td>
        <!-- 选择武器 -->
        <td style="width:50%">
          类型：
          <el-select v-model="weapon.type" @change="weaponTypeChange" placeholder="请选择武器类型">
            <el-option v-for="(item,i) in this.weapon.typeList" :key="i" :label="item" :value="item"></el-option>
          </el-select><br>
          <el-radio v-for="(item ,i) in weapon.list" 
            :key="i" :label="item" 
            v-model="weapon.choose"
            style="margin-top:20px">
            {{item}}
          </el-radio>
        </td>
      </tr> 
      <!-- 圣遗物主属性 ：花、羽毛为固定的。其他的都是百分比，不考虑治疗加成
        1-攻击力  2-生命值  3-防御力 4元素精通  5-元素加成  6-物理加成 7-暴击  8-爆伤   -->
      <tr>
        <td colspan="2">
          <table border="2" style="width:100%">
            <tr>
              <td>沙漏
                <el-select  v-model="shengyiwu1.c.value">
                  <el-option label="攻击力46.6%" value="1"></el-option>
                  <el-option label="防御力58.3%" value="2"></el-option>
                  <el-option label="生命值46.6%" value="3"></el-option>
                  <el-option label="元素精通187" value="4"></el-option>
                </el-select>
              </td>
              <td>杯子
                <el-select  v-model="shengyiwu1.d.value">
                  <el-option label="攻击力46.6%" value="1"></el-option>
                  <el-option label="防御力58.3%" value="2"></el-option>
                  <el-option label="生命值46.6%" value="3"></el-option>
                  <el-option label="元素加成46.6%" value="5"></el-option>
                  <el-option label="物理加成58.3%" value="6"></el-option>
                </el-select>
              </td>
              <td>头冠
                <el-select  v-model="shengyiwu1.e.value">
                  <el-option label="攻击力46.6%" value="1"></el-option>
                  <el-option label="防御力58.3%" value="2"></el-option>
                  <el-option label="生命值46.6%" value="3"></el-option>
                  <el-option label="元素精通187" value="4"></el-option>
                  <el-option label="暴击率31.1%" value="7"></el-option>
                  <el-option label="爆伤62.2%" value="8"></el-option>
                </el-select>
              </td>
            </tr>
          </table>
        </td>
      </tr>
      <!-- 圣遗物副词缀 -->
      <tr>
        <td colspan="2">
          <table style="width:100%">
            <tr>
              <td style="width:40%">固定攻击力<el-input v-model="this.shengyiwu2.gongjili"></el-input></td>
              <td style="width:60%">百分比攻击力<el-input v-model="this.shengyiwu2.gongjilijiacheng"></el-input>%</td>
            </tr>
            <tr>
              <td style="width:50%">固定生命值<el-input v-model="this.shengyiwu2.shengmingzhi"></el-input></td>
              <td style="width:50%">百分比生命值<el-input v-model="this.shengyiwu2.shengmingzhijiacheng"></el-input>%</td>
            </tr>
            <tr>
              <td style="width:50%">固定防御力<el-input v-model="this.shengyiwu2.fangyuli"></el-input></td>
              <td style="width:50%">百分比防御力<el-input v-model="this.shengyiwu2.fangyulijiacheng"></el-input>%</td>
            </tr>
            <tr>
              <td style="width:50%">———暴击率<el-input v-model="this.shengyiwu2.baojilvjiacheng"></el-input>%</td>
              <td style="width:50%">——爆伤加成<el-input v-model="this.shengyiwu2.baoshangjiacheng"></el-input>%</td>
            </tr>
          </table>
        </td>
      </tr> 
      <tr style="text-align:center">
        <el-button type="success" @click="confirm" style="margin-left:80%;">确认</el-button>
      </tr>
    </table>

    <el-dialog
        title="提示"
        :visible.sync="visibility"
        width="40%"
        :modal="true"
        :before-close="handleClose">
        <table border="2" style="width:60%;height:300px;margin-left:20%">
          <caption>暴击率:100% </caption>
          <tr>
            <td style="width:40%">不暴击</td>
            <td style="width:60%"></td>
          </tr>
          <tr>
            <td>暴击</td>
            <td></td>
          </tr>
        </table>
    </el-dialog>

  </div>
</template>

<script>
import {roleList1,roleList2,roleList3,roleList4,roleList5,roleList6,roleList7} from '@/assets/roleList.js'
import {weaponList1,weaponList2,weaponList3,weaponList4,weaponList5} from '@/assets/weaponList.js'
export default {
  name: 'HelloWorld',
  data () {
    return {
      //人物
      role : {
        typeList : ['冰','雷','水','火','风','岩','草'],// 人物属性：冰雷水火岩风草
        list : [], // 展示用角色列表
        type : '', //记录所选属性
        choose : '',//记录所选角色姓名
        myRole : {
          name: '',
          shengmingzhi: '',//生命值
          gongjili: '',//攻击力
          fangyuli: '',//防御力
          city: '',//城市
          birth: '',//生日
          yuansujiacheng: 0,//元素加成
          wulijiacheng: 0,//物理加成
          fangyulijiacheng: 0,//防御力加成
          gongjilijiacheng: 0,//攻击加成
          shengmingzhijiacheng : 0,//生命加成
          baojilvjiacheng: 0,//暴击率加成
          baoshangjiacheng: 0,//爆伤加成
          yuansujingtongjiacheng: 0//元素精通加成 
        },// 所选角色信息

      },
     //武器
     weapon : {
       typeList : ['长柄武器','单手剑','双手剑','法器','弓箭'],//武器类型
       type : '',//所选武器类型
       list : [],//展示武器选项
       choose : '',//所选武器名字
       myWeapon : {
         name : '',
         gongjili: 0,
        gongjilijiacheng: 0,//攻击力加成
        baojilvjiacheng: 0,//暴击率加成
        baoshangjiacheng: 0,//爆伤加成
        fangyulijiacheng: 0,//防御力加成
        shengmingzhijiacheng: 0,//生命值加成
        yuansuchongnengxiaolv: 0,
        yuansujingtongjiacheng : 0,
        texiao : '',
       }
     },
     //这里只记录圣遗物主词条
     shengyiwu1 : {
      //  a : '',
      //  b : '',
       c : { //沙漏
        value : '',
        gongjilijiacheng : 0,//攻击力加成
        shengmingzhijiacheng : 0,//生命加成
        fangyulijiacheng : 0,//防御力加成
        yuansujingtongjiacheng : 0,//元素精通
       },
       d :  { //杯子
        value : '',
        gongjilijiacheng : 0,//攻击力加成
        shengmingzhijiacheng : 0,//生命加成
        fangyulijiacheng : 0,//防御力加成
        yuansujiacheng : 0,//元素加成
       wulijiacheng : 0,//物理加成
       },
       e :  { //头冠
        value : '',
        gongjilijiacheng : 0,//攻击力加成
        shengmingzhijiacheng : 0,//生命加成
        fangyulijiacheng : 0,//防御力加成
        yuansujingtongjiacheng : 0,//元素精通
        baojilvjiacheng : 0,//暴击率
       baoshangjiacheng : 0//爆伤加成
       },
       gongjili : 311,//圣遗物固定攻击力（羽毛）
       shengmingzhi : 4780,//圣遗物固定生命值（花）
       gongjilijiacheng : 0,//攻击力加成
       shengmingzhijiacheng : 0,//生命加成
       fangyulijiacheng : 0,//防御力加成
       yuansujingtongjiacheng : 0,//元素精通
       yuansujiacheng : 0,//元素加成
       wulijiacheng : 0,//物理加成
       baojilvjiacheng : 0,//暴击率
       baoshangjiacheng : 0//爆伤加成
     },
     shengyiwu2 : {
       gongjili : '',
       gongjilijiacheng : '',
       fangyuli : '',
       fangyulijiacheng : '',
       shengmingzhi : '',
       shengmingzhijiacheng : '',
       baojilvjiacheng : '',
       baoshangjiacheng : ''
     },
     // 开启dialog
     visibility : false
    }
  },
  methods : {
    roleTypeChange () {
      switch(this.role.type){
        case "冰" :
          this.role.list = []
          for(var i in roleList1){
            this.role.list.push(roleList1[i].name)
          }
          break;
        case "雷" :
          this.role.list = []
          for(var i in roleList2){
            this.role.list.push(roleList2[i].name)
          }
          break;
        case "水" :
          this.role.list = []
          for(var i in roleList3){
            this.role.list.push(roleList3[i].name)
          }
          break;
        case "火" :
          this.role.list = []
          for(var i in roleList4){
            this.role.list.push(roleList4[i].name)
          }
          break;
        case "风" :
          this.role.list = []
          for(var i in roleList5){
            this.role.list.push(roleList5[i].name)
          }
          break;
        case "岩" :
          this.role.list = []
          for(var i in roleList6){
            this.role.list.push(roleList6[i].name)
          }
          break;
        case "草" :
          this.role.list = []
          for(var i in roleList7){
            this.role.list.push(roleList7[i].name)
          }
          break;
      }
      console.log('人物列表发生改变：'+this.role.list)
    },
    weaponTypeChange () {
      // console.log('所选武器类型：'+this.weapon.list)
      switch(this.weapon.type){
        case "长柄武器":
          this.weapon.list = []
          for(var  j in weaponList1){
            this.weapon.list.push(weaponList1[j].name)
          }
        break;
        case "单手剑":
          this.weapon.list = []
          for(var  j in weaponList2){
            this.weapon.list.push(weaponList2[j].name)
          }
        break;
        case "双手剑":
          this.weapon.list = []
          for(var  j in weaponList3){
            this.weapon.list.push(weaponList3[j].name)
          }
        break;
        case "法器":
          this.weapon.list = []
          for(var  j in weaponList4){
            this.weapon.list.push(weaponList4[j].name)
          }
        break;
        case "弓箭":
          this.weapon.list = []
          for(var j in weaponList5){
            this.weapon.list.push(weaponList5[j].name)
          }
        break;
      }
      console.log('武器列表发生改变：'+this.weapon.list)
    },
    //根据角色名字，查找角色信息
    findRoleInfoByName (searchName) {
      var temp = []
      //第一步筛选，减小查找范围
      switch(this.role.type){
        case '冰':temp=roleList1
        break
        case '雷':temp=roleList2
        break
        case '水':temp=roleList3
        break
        case '火':temp=roleList4
        break
        case '风':temp=roleList5
        break
        case '岩':temp=roleList6
        break
        case '草':temp=roleList7
        break
      }
      for(var i in temp){
        if(temp[i].name==searchName)
        return temp[i]
      }

    },
    //根据武器名字，查找武器信息
    findWeaponInfoByName (searchName) {
      var temp = []
      //第一步筛选，减小查找范围
      switch(this.weapon.type){
        case '长柄武器':temp=weaponList1
        break
        case '单手剑':temp=weaponList2
        break
        case '双手剑':temp=weaponList3
        break
        case '法器':temp=weaponList4
        break
        case '弓箭':temp=weaponList5
        break
      }
      for(var i in temp){
        if(temp[i].name==searchName)
        return temp[i]
      }

    },
    shengyiwu1Change(x){
      console.log("选择了"+this.shengyiwu1.c+this.shengyiwu1.d+this.shengyiwu1.e)
      switch (x) {
        case '1':
          this.shengyiwu1.gongjilijiacheng += 0.466
          break;
        case '2':
          this.shengyiwu1.fangyulijiacheng += 0.583
          break;
        case '3':
          this.shengyiwu1.shengmingzhijiacheng += 0.466
          break;
        case '4':
          this.shengyiwu1.yuansujingtongjiacheng += 187
          break;
        case '5':
          this.shengyiwu1.yuansujiacheng += 0.466
          break;
        case '6':
          this.shengyiwu1.wulijiacheng += 0.583
          break;
        case '7':
          this.shengyiwu1.baojilvjiacheng += 0.311
          break;
        case '8':
          this.shengyiwu1.baoshangjiacheng += 0.622
          break;
        
      }
    },
    confirm (){
      this.visibility = true
    },
    handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
  },
  computed : {
    roleName (){
      return this.role.choose
    },
    weaponName () {
      return this.weapon.choose
    }
  },
  watch : {
    roleName : {
      handler : function(){
        console.log("选中的角色:"+this.role.choose)
        this.role.myRole=this.findRoleInfoByName(this.roleName)
      }
    },
    weaponName : {
      handler : function(){
        console.log('选中的武器；'+this.weapon.choose)
        this.weapon.myWeapon = this.findWeaponInfoByName(this.weaponName)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-input {
  width: 200px;
}

</style>



